.list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.enter {
    opacity: 0;
    transform: translateX(-100%)
  }
  
  .enterActive {
    opacity: 1;
    transform: translateX(0);
    transition: all 250ms linear;
  }
  
  .exit {
    opacity: 1;
    transform: translateX(0);
  }
  
  .exitActive {
    opacity: 0;    
    transform: translateX(-100%);
    transition: all 250ms linear;
  }