.item {
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    padding: 0 15px;
    height: 50px;
    margin-bottom: 5px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.name {
    width: 50%;
}

.box {
    width: 50%;
    display: flex;
    justify-content: space-between;
}